@font-face {
   font-family: "Lineto-Circular";
   src:    url("../fonts/lineto-circular-book-s.eot");
   src:    url("../fonts/lineto-circular-book-s.eot?#iefix") format("embedded-opentype"),
     url("../fonts/lineto-circular-book-s.woff") format("woff"),
     url("../fonts/lineto-circular-book-s.ttf") format("truetype"),
     url("../fonts/lineto-circular-book-s.svg#LLCircularWeb-Book") format("svg");
   font-weight: 400;
   font-style: normal;
}

@font-face {
  font-family: "Lineto-Circular";
  src: url('../fonts/lineto-circular-medium-s.eot');
  src: url('../fonts/lineto-circular-medium-s.eot?#iefix') format('embedded-opentype'),
     url('../fonts/lineto-circular-medium-s.woff') format('woff'),
     url('../fonts/lineto-circular-medium-s.ttf') format('truetype'),
     url('../fonts/lineto-circular-medium-s.svg#CircularMedium') format('svg');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Lineto-Circular";
  src: url('../fonts/lineto-circular-bold-s.eot');
  src: url('../fonts/lineto-circular-bold-s.eot?#iefix') format('embedded-opentype'),
     url('../fonts/lineto-circular-bold-s.woff') format('woff'),
     url('../fonts/lineto-circular-bold-s.ttf') format('truetype'),
     url('../fonts/lineto-circular-bold-s.svg#CircularBold') format('svg');
  font-weight: 600;
  font-style: normal;
}

$col-xs: 576px;

$text-color-dark: black;
$text-color-light: white;
$text-color-gray: #aaaaaa;
$light-gray: #eeeeee;

$info-padding: 2rem;
$info-padding-small: 1.5rem;


body {
  background: white;
  overflow-x: hidden;
  font-family: 'Lineto-Circular', 'Times';
  // font-size: 13px;
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $text-color-dark;
}

#wrapper {
  background: white;
  overflow-x: hidden;
  min-height: 100vh;
}

h1 {
  font-size: 3rem;
}

h4 {
  font-size: 2rem;
}

div:focus {
  outline: 0;
}

@media (min-width: 1600px) {
    .container{
        width: 1570px;
    }
}

#app {
  background-color: white;
  transition: opacity 1s ease;
}

#image-glitch {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  & > div {
    width: inherit;
    height: inherit;
    position: absolute;
    top: 0;
    left: 0;
  }
}
#intro-logo {
  position: absolute;
  top: 50%;
  left: 0;
  color: white;
  font-size: 24rem;
  font-weight: 500;
  letter-spacing: 10px;
  text-align: center;
  width: 100%;
  transform: translate(0, -50%) scale(1, 1);
  transition: transform .2s ease;
  // line-height: 1.25;

  &.shrunk {
    transform: translate(0, -50%) scale(1, 0.05);
  }

  span {
    display: inline-block;
    position: relative;
    top: 0;
    left: 0;
    margin: 0 10px;

    &.second-row {
      margin: 0 25px;
    }

    span.spin {
      padding-left: 10px;
      transition: transform 1s ease;
      transform: rotate3d(0, 1, 0, 0deg);
      animation: rotate 2s 1 linear;
      margin: 0;

      &.loading {
        animation: rotate 2s infinite linear;
      }
    }
  }
}

@keyframes rotate {
    from {transform: rotate3d(0, 1, 0, 359deg);}
    to {transform: rotate(0, 1, 0, 0deg);}
}

@keyframes flicker {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      opacity:random();
    }
  }
}

#page-nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  font-size: 2rem;
  color: $text-color-light;
  z-index: 6;

  &.dark {

    a, span {
      color: $text-color-dark;
    }
  }

  a {
    display: inline-block;
    margin: 1rem 0 0 1.5rem;
    color: $text-color-light;
  }

  .main-menu-links {
    position: absolute;
    top: 0;
    right: 0;
    display: inline-block;

    span {
      cursor: pointer;
      display: inline-block;
      margin: 1rem 1.5rem 0 0;
    }
  }
}

.page-heading {
  text-align: center;
  padding: 12rem 0 6rem;
}

