.frontpage {
  .video-module {
    @media (min-width: 960px) and (min-height: 700px) {
      .klein-play {
        display: block;
      }
    }
  }
}

.video-module-wrap, .playlist-playarea {
  width: 100%;
  height: 100vh;
  background: black;
}

.video-module {
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 400px;
  background: linear-gradient(#151515, black);
  color: $text-color-light;
  overflow: hidden;

  a {
    color: $text-color-dark;
  }

  &.fullscreen {
    height: 100vh;
  }

  // @media (min-width: 960px) and (min-height: 700px) {
  //  .klein-play {
  //    display: none;
  //  }
  // }

  // Put play button stuff here!
  .big-play {
    width: 100vw;
    height: inherit;
    position: absolute;
    top: 0;
    left: 0;

    @media (max-width: 960px), (max-height: 700px) {
      & {
        display: none;
      }
    }

    & > div {
      position: relative;
      top: 50%;
      left: 0;
      text-align: center;
      font-size: 8rem;

      .ring {
        cursor: pointer;
        position: absolute;
        border: 4px solid $text-color-light;
        width: 150px;
          height: 150px;
          opacity: 1;
          text-align: center;
          border-radius: 50%;
          -moz-border-radius: 50%;
          -webkit-border-radius: 50%;
        transform: translate(-50%, -50%);
        transition: all .3s ease;
        outline: 0;

        &:focus {
          outline: 0;
        }

        &:hover {
          opacity: 0.25;
          width: 250px;
          height: 250px;
        }
      }

      .play {
        position: absolute;
        transform: translate(-40%, -48%);
        pointer-events: none;
      }
    }
  }

  a.disabled {
    pointer-events: none;
  }

  .video-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: inherit;
    height: inherit;
    min-height: 400px;
    background-position: center;
    background-size: cover;
    opacity: 0;
    -webkit-transition: opacity .1s ease;
    transition: opacity .5s ease;
    cursor: pointer;

    &[lazy=loading] {
      opacity: 0;
    }

    &[lazy=error] {
      opacity: 0;
    }

    &[lazy=loaded] {
      opacity: 0.9;
    }

    .top-gradient {
      height: 100px;
      background: -webkit-linear-gradient(rgba(0, 0, 0, 0.2), transparent);
      background: -o-linear-gradient(rgba(0, 0, 0, 0.2), transparent);
      background: linear-gradient(rgba(0, 0, 0, 0.2), transparent);
    }
  }

  .mini-info {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50px;
    color: $text-color-light;
    z-index: 5;

    &.minimized {
      display: none;
    }

    .video-hover, .suggestions-hover {
      height: inherit;
      display: flex;
      align-items: center;
      padding: 1rem 1.5rem;
    }

    .video-hover {
      position: absolute;
      color: $text-color-gray;
      left: 0;
      top: 0;
    }

    .suggestions-hover {
      position: absolute;
      right: 0;
      top: 0;
      font-size: 2.5rem;
    }
  }

  .info-bg-shade {
    pointer-events: none;
    display: none;
  }

  .info-bg-shade.dark {
    display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100vw;
      height: 55vh;
      background: linear-gradient(transparent,rgba(0,0,0,0.9));
  }

  .info.dark {
    background: transparent;
    color: white;

    .open-wrap, .next, .divider {
      border-left: none;
    }

    .suggestions .video .title {
      color: #aaaaaa;
      font-weight: normal;
    }
  }

  .info {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    max-height: 550px;
    opacity: 1;
    color: $text-color-dark;
    background: $text-color-light;
    transition: all .6s ease;
    border-bottom: 1px solid $light-gray;
    overflow: hidden;
    z-index: 3;

    &.minimized {
      max-height: 50px;
      opacity: 0.1;
    }

    &.force-down {
      max-height: 0;
      opacity: 0;
    }

    .info-table {
      display: table;
      position: relative;
      top: 0;
      left: 0;
      min-height: 200px;
      // padding: 3rem;
      transition: all .3s ease;

      @media (max-width: 960px), (max-height: 700px) {
        // & { padding: 2rem; }
      }

      .inner {
        display: table-row;

        .play-block {
          display: table-cell;
          position: relative;
          width: 100px;
          vertical-align: middle;
          padding: $info-padding 0 $info-padding 3rem;
          @media (max-width: 992px) {
            & {
              padding: $info-padding-small 0 $info-padding-small 2rem;
            }
          }

          > div {
            position: relative;
          }
        }

        .text-block {
          display: table-cell;
          position: relative;
          vertical-align: middle;
          padding: $info-padding 0;
          @media (max-width: 992px) {
            & {
              padding: $info-padding-small 0;
            }
          }
          min-width: calc(75vw - 6rem - 100px);
          @media (min-width: 1200px) {
            & { min-width: calc(66vw - 6rem - 100px); }
          }
          @media (min-width: 1600px) {
            & { min-width: calc(50vw - 6rem - 100px); }
          }

          .text {
            padding: 0 3rem;
            @media (max-width: 960px) {
              & {
                padding: 0 2rem;
              }
            }

            h1 {
              font-size: 3rem;
              max-width: 960px;
              margin: 0 0 2rem 0;

              @media (max-width: 960px) {
                & {
                  font-size: 2rem;
                  max-width: 768px;
                }
              }
            }

            p {
              max-width: 640px;
            }

            div.description {
              max-width: 720px;

              p {
                margin: 0;
              }
            }

            div.playlist-meta, div.video-meta {
              max-width: 640px;
              color: $text-color-gray;

              .date, .count, .duration {
                display: inline-block;
                width: 32%;
                // margin-right: 5rem;
              }

              .date {
                text-align: left;
              }

              .count {
                text-align: center;
              }

              .duration {
                text-align: right;
              }
            }
          }
        }

        .suggestions-block {
          display: table-cell;
          position: relative;
          vertical-align: top;
          border-left: 1px solid #eee;

          @media (max-width: 576px) {
            & {
              display: none;
            }
          }
        }
      }
    }
  }
}

.klein-play {
  width: 100px;
  height: 100px;

  .inner {
    position: absolute;
    top: 0;
    left: 0;
    width: inherit;
    height: inherit;
    text-align: center;
    font-size: 4rem;

    .centered {
      position: absolute;
      top: 50%;
      left: 50%;

      .progress-ring, .ring {
        cursor: pointer;
        position: absolute;
        top: 0;
        left: 0;
        width: 100px;
        height: 100px;
        transform: translate(-50%, -50%);
        opacity: 1;
        text-align: center;
        border-radius: 50%;
        -moz-border-radius: 50%;
        -webkit-border-radius: 50%;
        transition: all .3s ease;

        &:focus {
          outline: 0;
        }

        &:hover {
          opacity: 1;
          width: 130px;
          height: 130px;
        }
      }

      .progress-ring {
        background: transparent;
      }

      .ring {
        border: 2px solid $text-color-dark;
      }

      .play, .pause {
        position: absolute;
        top: 0;
        left: 0;
        pointer-events: none;
      }

      .play {
        transform: translate(-35%, -50%);
      }

      .pause {
        transform: translate(-45%, -50%);
      }
    }
  }
}

