.video {
  position: relative;
  display: table-cell;
  padding: $info-padding $info-padding $info-padding 0;
  @media (max-width: 992px) {
    & {
      padding: $info-padding-small $info-padding-small $info-padding-small 0;
    }
  }

  .thumb {
    width: calc((100vw - 16rem - 4rem) / 2);
    height: calc((100vw - 16rem - 4rem) / 2 / 1.77);
    background-color: #eeeeee;
    background-size: cover;
    background-position: center center;
    position: relative;

    img {
      height: inherit;
    }

    @media (min-width: 576px) {
      & {
        width: calc((100vw - 18rem - 3.5rem) / 3);
        height: calc((100vw - 18rem - 3.5rem) / 3 / 1.77);
      }
    }

    @media (min-width: 768px) {
      & {
        width: calc((100vw - 18rem - 5rem) / 4);
        height: calc((100vw - 18rem - 5rem) / 4 / 1.77);
      }
    }

    @media (min-width: 992px) {
      & {
        width: calc((100vw - 18rem - 10rem) / 5);
        height: calc((100vw - 18rem - 10rem) / 5 / 1.77);
      }
    }

    @media (min-width: 1200px) {
      & {
        width: calc((100vw - 18rem - 12rem) / 6);
        height: calc((100vw - 18rem - 12rem) / 6 / 1.77);
      }
    }

    @media (min-width: 1600px) {
      & {
        width: calc((100vw - 18rem - 14rem) / 7);
        height: calc((100vw - 18rem - 14rem) / 7 / 1.77);
      }
    }
  }

  .title-shade {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: inherit;
    padding-top: 0;
    background: rgba(0, 0, 0, 0.25);
    opacity: 0;
    transition: opacity .3s ease;

    &:hover {
      opacity: 1;
    }

    .play {
      color: white;
      font-size: 4rem;
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      text-align: center;
      width: 100%;
    }
  }

